/* Login Screen Styles */

@mixin shadow-raised {
  @include box-shadow(0 4px 10px 1px rgba(0, 0, 0, 0.2));

  &:before,
  &:after {
    content: none;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans";
}

#signUpContainer {
  width: 300px;
  margin: 50px auto;
  height: 300px;
  background-color: #ffffff;
  border-radius: 8px;
  @include shadow-raised();
}

#signUpHeader {
  height: 60px;
  /*background-color: #7db4b5;*/
  background-color: #3f51b5;
  border-radius: 8px 8px 0 0;
  box-shadow: inset 0px -5px 0px 0px rgba(224, 239, 241, 0.75);
  -webkit-transition: border 0.2s linear, box-shadow 0.2s linear,
    background-color 0.35s ease;
}

#signUpHeaderTitle {
  text-align: center;
  padding-top: 13px;
  font-size: 28px;
  font-family: "Damion";
  color: #ffffff;
}

#signUpFormContainer {
  padding: 20px 10px;
  position: relative;
}

#signUpForm {
  width: 90%;
  margin: 0px auto;
}

#signUpButton {
  width: 200px;
  height: 50px;
  /*background-color: rgba(125, 180, 181, 1);*/
  background-color: #3f51b5;
  color: white;
  font-size: 20px;
}

.signUpRow {
  margin: 15px 0;
  text-align: center;
}

.signUpRow input[type="checkbox"] {
  display: none;
}

.signUpRow input[type="checkbox"] + label::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 1px;
  height: 15px;
  width: 15px;
  /* border: 3px solid rgba(125, 180, 181, 0.85); */
  border: 3px solid #3f51b5;
  border-radius: 5px;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.signUpRow input[type="checkbox"]:checked + label::before {
  content: "\2713";
  font-size: 15px;
  line-height: 15px;
  /* color: rgba(125, 180, 181, 0.85); */
  color: #3f51b5;
}

.signUpRow input[type="checkbox"] + label {
  font-size: 12px;
  position: relative;
  padding: 3px 0 0 30px;
  cursor: pointer;
}

#sidebarsteve {
  width: 25%;
  float: left;

  border-right: 5px solid #e71d36;
}

#question-title {
  font-family: "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans",
    "DejaVu Sans", Verdana, "sans-serif";
  font-size: 24px;
  border-bottom: 2px solid blue;
  padding: 5px 45px 5px 5px;
  margin-left: 0px;
  display: inline;
  float: left;
}
body {
  height: 100%;
  background-color: #011627;
}
.topnav {
  overflow: hidden;
  background-color: #b3e5fb;
  border: 3px solid #673ab7;
  border-radius: 4px;
  width: 300px;
  display: block;
  margin: auto;
  box-shadow: 0px 0px 2px 3px #9adcfa inset;
}
.topnav a {
  float: right;
  width: 10%;
  display: block;
  color: blue;
  text-align: center;
  padding: 4px 6px;
  text-decoration: none;
  font-size: 24px;
}
.topnav a:hover {
  background-color: green;
}
.topnav input[type="text"] {
  width: 75%;
  float: left;
  padding: 0px;
  border: none;
  font-size: 18px;
  color: black;
  background-color: #b3e5fb;
  vertical-align: middle;
  margin: 10px 10px;
}

.sideDrawer {
  width: 250px;
}

.button {
  background-color: #4caf50;
  border: none;
  color: white;
  padding: 15px 25px;
  text-align: center;
  margin: auto;
  display: block;
  font-size: 16px;
  cursor: pointer;
  border-radius: 5px;
}
.question {
  display: inline-block;
  position: static;
  border: 1px solid black;
  border-radius: 5px;
  width: 100%;
  box-shadow: 2px 2px 3px #e91e63;
  /* margin: 0px auto; */
  margin-top: 25px;
  background-color: #fdecea;
}

.upvote {
  float: left;
  color: blue;
  background-color: green;
  border: none;
  margin: auto;
  display: block;
}
.columnA {
  width: 50px;
  height: 100%;
  /*float: left;*/
  border-right: 1px solid black;
  text-align: center;
}
.columnB {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 5px 10px 5px 10px;
}

.row {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 80px;
}

/* Hover effect on row for feedback */
.row:hover {
  background-color: #dfe3ee;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

